<template>
  <div class="d-flex"
       style="background-color: #eeeef0;">
    <v-card style="border-radius: 8px;width: 200px;background-color: #eeeef0;">
      <v-tabs vertical
              v-model="tab"
              style="background-color: #ffffff;"
              class="py-4">
        <v-tab class="tabs"
               v-for="(item,index) in tabList"
               style="color:black"
               :key="index">
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <span v-bind="attrs"
                    v-on="on"
                    style="width: 200px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        text-align: left;">{{index+1}}.{{item.title}}</span>
            </template>
            <span> {{index+1}}.{{ item.title }}</span>
          </v-tooltip>
        </v-tab>
      </v-tabs>
    </v-card>
    <v-card style='flex: 1;background-color: #eeeef0;border-radius: 8px'
            class='ml-4'>
      <v-tabs-items v-model="tab">

        <v-tab-item>
          <viewDataOfSubordinates />
        </v-tab-item>
      </v-tabs-items>
      <mrc_ess_submitBtn @success='success'
                         @cancel='cancel'></mrc_ess_submitBtn>
    </v-card>
  </div>

</template>

