import { required } from 'vuelidate/lib/validators'
import viewDataOfSubordinates from './viewDataOfSubordinates'
export default {
  components: { viewDataOfSubordinates },
  props: {},
  data () {
    return {
      tab: 0
    };
  },
  validations: {},
  computed: {
    tabList () {
      return [
        {
          title: this.$t('查看自己录入数据'),
          tableName: ''
        },
        {
          title: this.$t('查看下属人员数据'),
          tableName: ''
        },
        {
          title: this.$t('查看同岗位人员数据'),
          tableName: ''
        },
        {
          title: this.$t('查看被代理岗位人员数据'),
          tableName: ''
        },
        {
          title: this.$t('指定岗位查看指定组织架构人员数据'),
          tableName: ''
        },
        {
          title: this.$t('指定人员查看指定岗位人员数据'),
          tableName: ''
        },
        {
          title: this.$t('指定人员查看指定组织架构人员数据'),
          tableName: ''
        },
        {
          title: this.$t('指定人员查看指定人员错误'),
          tableName: ''
        },
      ]
    }
  },
  created () { },
  watch: {},
  mounted () { },

  methods: {
    success () {

    },
    cancel () {

    }
  },
};
</script>

<style scoped>
.tabs {
  width: 200px;
  padding: 0;
  text-align: left;
  padding-left: 28px;
}
.v-tabs--vertical > .v-tabs-bar .v-tab {
  height: 36px;
}
.v-tabs--vertical > .v-tabs-bar .v-tabs-slider {
  height: 36px;
  width: 4px;
}
</style>
